import DeleteIcon from '@material-ui/icons/Delete'

const Note = props => {
  const handleClick = () => props.onDelete(props.id);

  return (
    <div className="note">
      <h1>{props.title}</h1>
      <p>{props.content}</p>
      <button onClick={handleClick}><DeleteIcon/></button>
    </div>
  );
}

export default Note;
